* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Montserrat, sans-serif;
  letter-spacing: .01rem !important;
}

body {
  overflow: hidden;
}

.viewport {
  background-color: #f6f8fc;
  width: 100%;
  height: 100vh;
  display: flex;
}

.nav {
  background-color: #319946;
  flex-direction: column;
  flex-basis: 20rem;
  padding: 1rem 0;
  display: flex;
}

.nav-btn {
  background-color: #0000;
  border: none;
  align-items: center;
  gap: 1.5rem;
  padding: 1rem 1.6rem;
  display: flex;
}

.nav-btn:hover {
  cursor: pointer;
  background-color: #439c70;
  border-left: .3rem solid #fff;
  padding: 1rem 1.4rem;
}

.nav-btn:hover .nav-icon {
  width: 1.75rem;
}

.nav-btn:hover .nav-btn-text {
  font-weight: 700;
}

.nav-icon {
  aspect-ratio: 1;
  width: 1.5rem;
  transition: all .3s;
}

.nav-btn-text {
  color: #fff;
  font-size: .9rem;
  font-weight: 400;
}

.content {
  flex: 1;
}

.checkbox-container {
  gap: .5rem;
  display: flex;
}

.input-form .text-input {
  border: .05rem solid #319946;
  border-radius: 1.5rem;
  width: 100%;
  padding: 1.8rem;
  font-size: 1.5rem;
}

.button-full {
  color: #fff;
  background-color: #319946;
  border: none;
  border-radius: 1.5rem;
  padding: 1.8rem;
  font-size: 2rem;
}

.button-full:hover {
  background-color: #439c70;
}

.spinner {
  height: 5rem;
  margin-left: auto;
  margin-right: auto;
  animation: 2s linear infinite rotate;
  display: block;
}

@keyframes rotate {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.spinner-container {
  background-color: #fff;
  flex-direction: column;
  justify-content: center;
  display: flex;
  position: absolute;
  inset: 0;
}

.spinner-text {
  text-align: center;
  margin-top: 1rem;
  font-size: 2rem;
  font-weight: 500;
}

.hidden {
  display: none !important;
}
